.viec-lam {
	padding: 30px 0; }
.viec-lam-title {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	box-shadow: 0 1px 0 0 $title-color; }
.viec-lam-tabs {
	margin-right: -15px;
	li {
		display: inline-block;
		list-style: none;
		text-transform: uppercase;
		font-weight: 600;
		line-height: 30px;
		padding: 0 15px;
		position: relative;
		cursor: pointer;
		a {
			color: inherit; }
		&:before {
			background: url('../img/icon/Path 30.png');
			width: 15px;
			height: 30px;
			position: absolute;
			left: -15px;
			top: 0; }
		&:not(:first-child) {
			margin-left: 10px; }
		&.active,
		&:hover {
			color: #fff;
			background: $title-color;
			&:before {
				content: ''; } } } }
.content-header {
	margin-top: 10px;
	line-height: 40px;
	font-weight: 700;
	text-align: center;
	background: $title-color;
	color: #fff;
	border-bottom: 1px solid #ccc;
	div:first-child {
		text-align: left; } }
.tabs-content-wrap {
	&:not(.active) {
		display: none; } }
.content-item {
	word-wrap: break-word;
	text-transform: uppercase;
	padding: 12px 0;
	color: $title-color;
	align-items: center;
	border: 1px solid #ccc;
	border-top: none;
	.congviec {
		text-align: left;
		font-weight: 600; }
	.luong {
		text-align: center;
		color: #ff9f1a;
		font-weight: 900; }
	.diadiem {
		text-align: center;
		color: #6c5ce7;
		font-weight: 600; }
	a {
		color: inherit; }
	&:nth-child(even) {
		background: $fourth-color; }
	&:nth-child(odd) {
		background: #fff; }
	&:hover {
		border-left: none;
		border-right: none;
		background: #007DC6;
		transition: all .1s ease-in;
		* {
			color: #fff; } } }

.content-bottom {
	margin-top: 10px;
	box-shadow: 0 -1px 0 0 $title-color;
	.bottom-left {
		color: #fff;
		font-size: 15px;
		height: 30px;
		background: $title-color;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		&:after {
			content: '';
			background: url('../img/icon/Path 31.png');
			width: 15px;
			height: 30px;
			position: absolute;
			right: -15px;
			top: 0; }
		.info {
			display: flex;
			align-items: center;
			position: relative;
			p:nth-child(2) {
				margin-left: 10px; }
			span {
				&.mdi {
					position: absolute; }
				&:nth-child(2) {
					margin-left: 20px; } } } }
	.bottom-right {
		padding-right: 0; } }
@media (max-width: 991.97px) {
	.content-bottom {
		.bottom-left {
			&:after {
				content: none; }
			.bottom-right {
				padding-right: 15px; } } } }
@media (max-width: 767.97px) {
	.viec-lam {
		padding: 15px 0; }
	.viec-lam-title {
		flex-direction: column;
		.title {
			margin: 0 auto 15px; } }
	.content-header {
		div:first-child {
			text-align: center; } }
	.content-item {
		div:first-child {
			text-align: center;
			margin-bottom: 10px; } }
	.content-bottom {
		.bottom-left {
			font-size: 14px;
			height: auto;
			padding-top: 10px;
			padding-bottom: 10px; } } }
@media (max-width: 575.97px) {
	.viec-lam-tabs {
		margin: 0;
		width: 100%;
		display: flex;
		overflow-x: scroll;
		padding-left: 15px;
		// margin-bottom: 10px
		// padding-bottom: 5px
		li {
			white-space: nowrap; } }
	.content-bottom {
		.bottom-left {
			display: block;
			line-height: 1.5;
			.info {
				display: block;
				p {
					margin: 0 !important; } } }
		.bottom-right {
			padding-right: 15px;
			margin-top: 10px; } } }
