.donhangnoibat {
	.title {
		margin-top: 30px; }
	.sub-title {
		font-size: 16px;
		text-align: center;
		font-weight: 600;
		padding: 10px 0; }
	&-wrapper {
		padding-top: 30px;
		overflow: auto;
		display: flex;
		@media screen and ( min-width: 768px ) {
			flex-wrap: wrap;
			justify-content: space-around; } }
	&-item {
		flex: 0 0 240px;
		width: 240px;
		margin: 0 10px 20px;
		&:hover {
			transition: transform .3s ease-in-out;
			@media screen and ( min-width: 768px ) {
				box-shadow: 0 0 10px 0 rgba(#000, .3);
				transform: translateY(-4px);
				.donhangnoibat-content {
					box-shadow: none; } }
			.parallelogram::before {
				left: -180%; } }
		&:nth-of-type(1) {
			.sub-title {
				background: #55efc4;
				color: #364b44; }
			&:hover {
				.donhangnoibat-content {
					background: rgba(#55efc4, .15); } } }
		&:nth-of-type(2) {
			.sub-title {
				background: #ffeaa7;
				color: #008b60; }
			&:hover {
				.donhangnoibat-content {
					background: rgba(#ffeaa7, .15); } } }
		&:nth-of-type(3) {
			.sub-title {
				background: #ff7675;
				color: #fff; }
			&:hover {
				.donhangnoibat-content {
					background: rgba(#ff7675, .15); } } }
		&:nth-of-type(4) {
			.sub-title {
				background: #fffa65;
				color: #43be8c; }
			&:hover {
				.donhangnoibat-content {
					background: rgba(#fffa65, .15); } } }
		a {
			color: #007dc6; } }
	&-content {
		padding: 0 12px 12px;
		overflow: hidden;
		box-shadow: inset 0 0 3px 0 rgba(0, 0, 0, .3);
		p {
			margin: 10px 0; }
		.parallelogram {
			margin-top: 10px;
			position: relative;
			width: 100%;
			height: 150px;
			overflow: hidden;
			transform: skew(-20deg);
			&::before {
				z-index: 1;
				position: absolute;
				top: 0;
				left: 180%;
				width: 100%;
				height: 100%;
				transform: skew(45deg);
				background: rgba(255, 255, 255, 0.5);
				content: "";
				transition: .6s; }
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				transform: skew(20deg); } } } }
