.dao-tao-giao-duc {
	display: flex;
	align-items: center;
	min-height: 350px;
	padding: 40px 0;
	background: url('../img/backdrop/ryo-yoshitake-rD28m9BmXVs-unsplash.jpg') center center no-repeat fixed;
	background-size: cover;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(#000, .25); }
	.title {
		color: #fff; }
	.content {
		font-size: 1.5rem;
		line-height: 1.25;
		text-align: center;
		width: 85%;
		color: #fff;
		text-shadow: rgba(0, 0, 0, 0.4) 0px 0 4px;
		margin-top: 20px;
		margin-left: auto;
		margin-right: auto; } }
@media (max-width: 767.97px) {
	.dao-tao-giao-duc {
		padding: 40px 0;
		.content {
			font-size: 18px;
			margin-top: 15px;
			width: 90%; } } }
