$title-color: #007DC6;
$main-color: #38A8D9;
$text-color: #666666;
$second-color: #00B1FF;
$third-color: #A5EBFF;
$fourth-color: #ECFBFF;
$fifth-color: #DDDDDD;
$sixth-color: #E1E1E1;
$seventh-color: #F1F1F1;
$eighth-color: #B9B9B9;

::-moz-selection {
	color: #fff;
	background: $title-color; }
::selection {
	color: #fff;
	background: $title-color; }
*, ::before, ::after {
	box-sizing: border-box; }

body {
	font-family: 'MiraiHumanFont1', sans-serif;
	color: $text-color;
	font-weight: 400;
	font-size: 16px;
	overflow-x: hidden; }
a {
	text-decoration: none; }
strong {
	font-weight: bold; }
i {
	font-style: italic; }
img {
	max-width: 100%;
	height: auto; }

.title {
	font-weight: 900;
	font-size: 28px;
	line-height: 1.5;
	text-transform: uppercase;
	color: #007DC6; }
.sub-title {
	font-weight: 700;
	font-size: 24px;
	text-transform: uppercase; }
.color-title {
	color: #007DC6; }
.fw-700 {
	font-weight: 700; }
.fw-900 {
	font-weight: 900; }
.w-75 {
	width: 75%; }
.text-center {
	text-align: center; }
.text-italic {
	font-style: italic; }
.see-more {
	display: inline-block;
	margin-top: 5px;
	color: #007DC6; }
.tabs-pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	@media screen and ( min-width: 992px ) {
		justify-content: flex-end; }
	a {
		line-height: 30px;
		display: block;
		margin: 0 10px;
		color: $text-color;
		&.isDisabled {
			color: #DDD;
			pointer-events: none; }
		&.current-page {
			position: relative;
			color: #fff;
			&:before {
				content: '';
				z-index: -1;
				width: 20px;
				height: 20px;
				background: url('../img/icon/Polygon 1.png');
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%); } } } }
.posted {
	margin-top: 7px;
	font-size: 12px;
	position: relative;
	white-space: nowrap; }
.line {
	margin: 30px 0 10px;
	width: 100%;
	height: 2px;
	background: $seventh-color; }
.navbar-center {
	margin: 15px 0 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	a {
		color: $text-color; }
	li {
		list-style: none;
		background: $seventh-color;
		padding: 10px 15px;
		text-transform: uppercase;
		font-weight: 700;
		transition: all .1s ease-in-out;
		&:not(:first-child) {
			margin-left: 2px; }
		&.active, &:hover {
			background: $title-color;
			a {
				color: #fff; } } } }
.form-item {
	width: 100%;
	margin-bottom: 15px;
	label {
		color: #007DC6; }
	input, textarea {
		margin-top: 7px;
		width: 100%;
		padding: 8px 15px;
		background: $seventh-color;
		border: 1px solid $eighth-color;
		border-radius: 4px;
		font-family: 'MiraiHumanFont1', sans-serif;
		font-weight: 700;
		color: #007DC6;
		&::placeholder {
			color: $eighth-color;
			font-weight: 400; } }
	textarea {
		height: 150px; } }
.flex-box {
	display: flex;
	align-items: center;
	justify-content: flex-start; }
.transformY-center {
	position: relative;
	top: 50%;
	transform: translateY(-50%);
	z-index: 1; }
.view-mode {
	a {
		color: #0d151c !important;
		font-size: 24px; } }
.border-bottom {
	border-bottom: 1px solid silver; }
.page-banner {
	img {
		display: block;
		width: 100%; } }
@media (max-width: 991.98px) {
	.title {
		font-size: 24px; }
	.sub-title {
		font-size: 18px; } }
@media (max-width: 767.98px) {
	.title {
		font-size: 18px; }
	.sub-title {
		font-size: 16px; }
	.navbar-center {
		flex-wrap: wrap;
		margin-bottom: 15px;
		li {
			margin-bottom: 2px; } } }


@import "stylesheet/_mixins.sass";
@import "header/index.sass";
@import "footer/index.sass";

.tuyensinh {
	a {
		color: inherit; }
	&-container {
		line-height: 1.5;
		background: lighten(#007dc6, 55); }
	&-banner {
		overflow: hidden;
		img {
			width: 100%; }
		&__header {
			color: #007DC6;
			font-weight: 700;
			font-size: 32px;
			margin-top: 1rem;
			line-height: 1;
			@media screen and ( min-width: 768px ) {
				margin-top: 0; }
			@media screen and ( min-width: 992px ) {
				font-size: 40px; } }
		&__content {
			font-size: 1.25rem /* 20/16 */;
			font-weight: 600;
			line-height: 1.5;
			margin-top: 1rem;
			p {
				margin-top: 0.5rem; }
			@media screen and ( min-width: 768px ) {
				margin-top: 1.5rem; } }
		.swiper-pagination {
			width: 100%; } }
	&-title {
		color: #007DC6;
		font-size: 2.5rem;
		font-weight: 700;
		font-family: 'Times New Roman', Times, serif;
		padding: 0 10px 4px;
		box-shadow: 0 4px 0 0 #1D0029; }
	&-description {
		font-size: 20px;
		font-weight: 600;
		p:first-child::first-letter {
			line-height: 1;
			font-size: 3rem !important;
			float: left;
			font-family: Georgia, 'Times New Roman', Times, serif;
			padding-right: 1rem;
			padding-top: 4px;
			font-weight: 900;
			color: #000; }
		.about-hinhanhchutich {
			width: 380px; } }
	&-text {
		font-size: 20px;
		font-weight: 600;
		p + p {
			margin-top: 1rem; } }
	&-block-1 {
		background: url('../img/sakura-1.png') center center fixed;
		background-size: cover;
		font-size: 20px;
		font-weight: 600;
		color: #050709;
		p + p {
			margin-top: 1rem; } }
	&-link {
		font-size: 20px;
		display: inline-block;
		color: darken(#007DC6, 20); }
	&-button {
		color: inherit;
		background: #fff;
		text-transform: uppercase;
		padding: 6px 16px;
		text-align: center;
		font-size: 20px;
		border: 2px solid #007DC6;
		border-radius: 99px;
		box-shadow: 0 0 0 4px #fff, 0 0 8px 0 #000;
		.icon {
			color: #007DC6; } } }
.ct-tuyensinh {
	&__container {
		background: #fff;
		box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.3);
		padding: 2rem;
		@media screen and ( min-width: 576px ) {
			padding: 3rem; }
		@media screen and ( min-width: 992px ) {
			padding: 3rem 6rem; } }
	&__banner {
		display: block;
		width: 100%; }
	&__title {
		line-height: 1;
		font-size: 1.25rem;
		text-transform: uppercase;
		font-weight: 900;
		@media screen and ( min-width: 768px ) {
			font-size: 2rem; }
		p {
			&:nth-of-type(2) {
				font-size: 90%; }
			&:nth-of-type(3) {
				font-size: 80%; } } }
	&__divider {
		background: darken(#e0f3ff, 20);
		height: 2px;
		margin: 0 auto; }
	&__shortDescription {
		@media screen and ( min-width: 768px ) {
			font-size: 1.25rem; } }
	&__chitiet {
		li {
			margin-bottom: 0.5rem; }
		.bullets,
		.numbers {
			margin-left: 1rem;
			@media screen and ( min-width: 768px ) {
				margin-left: 2.5rem; } }
		.bullets {
			list-style: disc; }
		.numbers {
			list-style: decimal; } } }
.ds-donhang {
	&__content {
		.content-item {
			display: flex;
			flex-wrap: wrap;
			&:first-of-type {
				border-top: 1px solid #ccc; } } }
	&__headline {
		@media screen and ( min-width: 576px ) {
			display: flex;
			align-items: flex-end;
			justify-content: space-between; } }
	&__count {
		white-space: nowrap;
		text-align: center;
		line-height: 1.25;
		@media screen and ( min-width: 576px ) {
			text-align: left; }
		.counter {
			font-size: 32px;
			font-weight: 900;
			color: #007DC6; } }
	&__search {
		display: flex;
		font-weight: 600;
		position: relative;
		color: #fff;
		padding: 0 15px;
		justify-content: space-between;
		background: $title-color;
		text-transform: uppercase;
		@media screen and ( min-width: 576px ) {
			justify-content: center;
			margin-left: 15px;
			padding: 0 10px;
			&:before {
				background: url('../img/icon/Path 30.png');
				width: 15px;
				height: 30px;
				position: absolute;
				left: -15px;
				top: 0;
				content: ''; } }
		input, button {
			font-size: 17px;
			font-family: inherit;
			border: none;
			background: none;
			color: #fff;
			outline: none;
			height: 40px;
			@media screen and ( min-width: 576px ) {
				height: 30px; } }
		button {
			width: 30px; }
		input {
			font-weight: 700;
			&::placeholder {
				font-weight: 400;
				color: #fff; } } }
	&__list {
		display: none;
		@media screen and ( min-width: 768px ) {
			display: block; }
		a {
			padding: 4px 0;
			display: block;
			color: inherit;
			line-height: 1.5;
			text-transform: uppercase; }
		li {
			&.active,
			&:hover {
				a {
					margin-left: 4px;
					font-weight: 700;
					color: #007DC6;
					box-shadow: 0 1px 0 0 #007DC6;
					&::before {
						color: #007DC6;
						content: '\F0142';
						display: inline-block;
						font: normal normal normal 24px/1 'Material Design Icons';
						font-size: inherit;
						text-rendering: auto;
						line-height: inherit;
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale; } } } } } }

